import { shape, string } from 'prop-types';
import React from 'react';
import { useSelector } from 'react-redux';

import { Page, Job as Details } from 'components/widgets';

import { selectById } from './selectors';

const renderJob = ({
  components: { Container },
  elements: {
    ordered: [, ...fragments],
  },
  details: { title },
}) => (
  <Container>
    <h1>{title}</h1>
    {fragments}
  </Container>
);

const Job = ({ pageContext: { id } }) => {
  const job = useSelector(selectById(id));
  const { title, description } = job;

  return (
    <Page title={title} description={description}>
      <section>
        <Details {...job}>{renderJob}</Details>
      </section>
    </Page>
  );
};

Job.propTypes = {
  pageContext: shape({
    id: string.isRequired,
  }).isRequired,
};

Job.defaultProps = {};

export default Job;
